* {
  font-family: "Hind Vadodara", sans-serif;
}

body {
  background-color: #eee;
  padding-top: 60px;
}

.enlarge {
  transition: 0.3s;
}

.enlarge:hover {
  transform: scale(1.2);
}

.float-up {
  transition: 0.3s;
}

.float-up:hover {
  transform: translateY(-5px);
}

#contact-page {
  background: url("bg-contact.jpg");
}
